import {Action} from '@ngrx/store';

// travel agent

export const TRY_FETCH_TRAVELAGENT = 'TRY_FETCH_TRAVELAGENT';
export const SET_TRAVELAGENT = 'SET_TRAVELAGENT';
export const TRAVELAGENT_EDIT = 'TRAVELAGENT_EDIT';
export const UPLOAD_TRAVELAGENT_IMAGES = 'UPLOAD_TRAVELAGENT_IMAGES';


// classes for travel agent
export class TryFetchTravelAgent implements Action {
    readonly type = TRY_FETCH_TRAVELAGENT;
}

export class SetTravelAgent implements Action {
    readonly type = SET_TRAVELAGENT;

    constructor(public payload: {}) {
    }
}

export class UploadTravelAgentImages {
    readonly type = UPLOAD_TRAVELAGENT_IMAGES;

    constructor(public payload: any) {
    }
}

export class TravelAgentEdit implements Action {
    readonly type = TRAVELAGENT_EDIT;

    constructor(public payload: {}) {
    }
}



export type TravelagentActions =
    TravelAgentEdit | UploadTravelAgentImages | SetTravelAgent | TryFetchTravelAgent ;
