
import {TravelagentModel} from '../travelagent.model';
import {TravelagentActions} from './travelagent.actions';
import {AppState} from "../../../../store/app.reducer";

export interface TravelAgentState extends AppState {
    travelAgent: State;
}

export interface State {
    travelAgent: TravelagentModel;
}

const initialState: State = {
    travelAgent: new TravelagentModel('', '', '', '', '', '', '', '', '', '', {
        permanentAddress: {
            address: '',
            pincode: '',
            phoneNo: ''
        }
    }, {
        profileRegistrar: '', name: '', mobile: '', email: '', pan: '', address: {
            address: '',
            phoneNo: '',
            pincode: ''
        }, bankDetails: {
            bankAccountNo: '',
            bankAddress: '', bankIfsc: '', bankName: ''
        }
    }, '', '', '')
};

export function TravelAgentReducer(state = initialState, action: TravelagentActions) {
    switch (action.type) {
        case 'SET_TRAVELAGENT': {
            return {
                ...state,
                travelAgent: action.payload
            };
        }
        default: {
            return {
                ...state
            };
        }

    }
}
