import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import {TravelagentComponent} from './travelagent.component';
import {TravelagenteditComponent} from './travelagentedit/travelagentedit.component';


const routes: Routes = [
  {
    path: '', component: TravelagentComponent, children: [
      {path: 'dashboard', component: TravelagentComponent},
      {path: 'editProfile', component: TravelagenteditComponent}
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class TravelagentRoutingModule { }
