import {Injectable} from '@angular/core';
import {Actions, Effect, ofType} from '@ngrx/effects';
import {map, switchMap} from 'rxjs/operators';
import {HttpClient, HttpResponse} from '@angular/common/http';
import * as endPoints from '../../../../shared/serverEndpoints';
import * as TravelagentActions from './travelagent.actions';

@Injectable()
export class TravelagentEffects {
    constructor(public action: Actions, public http: HttpClient) {
    }

// effects for travel agent
    @Effect()
    editTravelAgent = this.action.pipe(
        ofType(TravelagentActions.TRAVELAGENT_EDIT),
        map((payload: TravelagentActions.TravelAgentEdit) => {
            return payload.payload;
        }),
        switchMap((data) => {
            return this.http.post(endPoints.edittravelagent, data, {observe: 'response'});
        }),
        switchMap((response: HttpResponse<any>) => {
            if (response.status === 200) {
                return [
                    {
                        type: TravelagentActions.SET_TRAVELAGENT,
                        payload: response.body
                    }
                ];
            }
        })
    );


    @Effect()
    uploadTravelAgentImages = this.action.pipe(
        ofType(TravelagentActions.UPLOAD_TRAVELAGENT_IMAGES),
        map((data: TravelagentActions.UploadTravelAgentImages) => {
            return data.payload;
        }), switchMap((data: { registrationCinImage: File, registrationGstImage: File, registrationMsmeImage: File, registrationRcImage: File }) => {
            return this.http.post(endPoints.uploadtravelagentImages, data, {observe: 'response'});
        }),
        switchMap((response: HttpResponse<any>) => {
            if (response.status === 200) {
                return [
                    {
                        type: TravelagentActions.SET_TRAVELAGENT,
                        payload: response.body
                    }
                ];
            }
        })
    );

    @Effect()
    fetchTravelAgent = this.action.pipe(
        ofType(TravelagentActions.TRY_FETCH_TRAVELAGENT),
        switchMap(() => {
            return this.http.get(endPoints.fetchtravelagent, {observe: 'response'});
        }),
        switchMap((response: HttpResponse<any>) => {
            if (response.status === 200) {
                return [
                    {
                        type: TravelagentActions.SET_TRAVELAGENT,
                        payload: response.body
                    }
                ];
            }
        })
    );

}
